                  <v-spacer></v-spacer>
                  <v-text-field
                    v-model="search"
                    @input="onSearchChange"
                    append-icon="fa-search"
                    label="Search"
                    single-line
                    hide-details
                  ></v-text-field>
                </v-toolbar>
              </template>
              <template v-slot:[`item.created_on`]="{ item }">
                {{ item.created_on | timezone }}
              </template>
              <template v-slot:[`item.updated_on`]="{ item }">
                {{ item.updated_on | timezone }}
              </template>
              <template v-slot:[`item.ts_value`]="{ item }">
