              </template>
              <template v-slot:[`item.enable`]="{ item }">
                <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.cov_threshold`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.cov_threshold"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.cov_threshold }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.cov_threshold"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.history_enable`]="{ item }">
                <v-checkbox v-model="item.history_enable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.history_interval`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.history_interval"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.history_interval }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.history_interval"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.history_type`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.history_type"
                  @save="updatePoint(item)"
