                  <b>{{ item.history_type }}</b>
                  <template v-slot:input>
                    <v-select v-model="item.history_type" :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']"></v-select>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.input_min`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.input_min"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
