                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.input_max`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.input_max"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
