                  <b>{{ item.input_max }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.input_max"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.scale_min`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.scale_min"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.scale_min }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.scale_min"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.scale_max`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.scale_max"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.scale_max }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.scale_max"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.value_round`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.value_round"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.value_round }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.value_round"
                      single-line
                      type="number"
                      min="0"
                      @input="numberChange()"
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.value_operation`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.value_operation"
                  @save="updatePoint(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.value_operation }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.value_operation" single-line type="string"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.writable`]="{ item }">
                <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.tags`]="{ item }">
                <ul style="cursor: pointer" @click="editItem(item)" class="overflow-td">
                  <li v-for="(key, index) of item.tags ? Object.keys(JSON.parse(item.tags)) : []" :key="index">
                    {{ key }} : {{ JSON.parse(item.tags)[key] }}
                  </li>
                </ul>
              </template>
              <template v-slot:[`item.priority_array_write`]="{ item }">
                <div class="overflow-td">
                  <span
                    v-for="(key, index) of item.priority_array_write ? Object.keys(item.priority_array_write) : []"
                    :key="index"
                  >
                    {{ key }}:&nbsp;<span class="value">{{
                      item.priority_array_write[key] || item.priority_array_write[key] == 0
                        ? item.priority_array_write[key]
                        : 'null'
                    }}</span
                    >,&nbsp;
                  </span>
                </div>
              </template>
              <template v-slot:[`item.disable_mqtt`]="{ item }">
                <v-checkbox v-model="item.disable_mqtt" @click="updatePoint(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.actions`]="{ item }">
                <v-icon small @click="editItem(item)" color="primary">fa-pencil</v-icon>
                <v-icon small @click="clickDelete(item)" color="error">fa-trash-o</v-icon>
                <v-icon small @click="openPointWriteDialog(item)" color="orange">fa-yelp</v-icon>
              </template>

              <!-- headers -->
              <template v-slot:[`header.enable`]>
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.history_enable`]>
