                <v-icon x-small @click="openMassEditDialog('history_enable'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.history_interval`]>
                {{ toUpperTextKey('history_interval') }}&nbsp;
                <v-icon
                  x-small
                  @click="openMassEditDialog('history_interval'), $event.stopPropagation()"
                  color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.history_type`]>
                {{ toUpperTextKey('history_type') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('history_type'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.cov_threshold`]>
                {{ toUpperTextKey('cov_threshold') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('cov_threshold'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.input_min`]>
