                <v-icon x-small @click="openMassEditDialog('writable'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.tags`]>
                {{ toUpperTextKey('tags') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('tags'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
