                {{ toUpperTextKey('disable_mqtt') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('disable_mqtt'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.fallback_value`]>
                {{ toUpperTextKey('fallback_value') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('fallback_value'), $event.stopPropagation()" color="primary"
                  >fa-pencil</v-icon
                >&nbsp;
