    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-combobox
                    ref="comboBox"
                    label="Point name"
                    v-model="editedItem.name"
                    :items="typeNames"
                    required
                    :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"
                    @change="onChangeTypeName"
                    @input="val => (editedItem.name = conventionNaming(val, setting))"
                  />
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.history_enable" label="History enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.writable" label="Writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-checkbox v-model="editedItem.disable_mqtt" label="disable mqtt"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-select
                    v-model="editedItem.history_type"
                    :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']"
                    label="History type"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.history_interval"
                    label="History interval"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.cov_threshold"
                    label="Cov threshold"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation"> </v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.input_min"
                    label="Input min"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.input_max"
                    label="Input max"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.scale_min"
                    label="Scale min"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.scale_max"
                    label="Scale max"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" md="3">
                  <v-text-field
                    v-model="editedItem.value_round"
                    label="Value round"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.fallback_value"
                    label="Fallback Value"
                    type="number"
                    hint="The empty value will return null"
                  ></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.write_value" label="Write Value" type="number" min="0" @input="numberChange()" ></v-text-field>
                </v-col> -->
                <v-col cols="12" sm="12" md="12">
                  <v-text-field value="Tags" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <rd-point-tags :on-change="onChangeTag" :items="tags"></rd-point-tags>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>fa-times-circle</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn
              x-small
              class="mr-1 btn-height-22 white--text"
              color="primary"
              @click="clickContinue()"
              :disabled="!dataImport"
            >
              Continue
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="pointHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="fa-search"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> fa-trash-o </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    >fa-check-circle-o</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="createPoint(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    >fa-refresh</v-icon
                  >
                </template>
                <template v-slot:[`item.name`]="{ item }">
                  <v-text-field
                    v-model="item.name"
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    @input="val => (item.name = conventionNaming(val, setting))"
                  />
                </template>
                <template v-slot:[`item.priority_array_write`]="{ item }">
                  {{
                    item.priority_array_write && typeof item.priority_array_write == 'object'
                      ? JSON.stringify(item.priority_array_write)
                      : item.priority_array_write
                  }}
                </template>
              </v-data-table>
            </v-form>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="secondary" @click="e1 = 1">Back</v-btn>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="saveImport" :disabled="!valid"
              >Save</v-btn
            >
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog
      :is-open="dialogImportExcel"
      :on-close="() => (dialogImportExcel = false)"
      :on-confirm="convertExcelToJson"
    ></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <rd-point-tags v-if="editField === 'tags'" :on-change="onChangeTag" :items="tags"></rd-point-tags>
          <v-select
            v-if="editField === 'history_type'"
            v-model="editAll[editField]"
            :items="['COV', 'INTERVAL', 'COV_AND_INTERVAL']"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            v-model.number="editAll[editField]"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="numberChange()"
          ></v-text-field>
          <v-text-field
            v-if="typeof editAll[editField] === 'string' && editField !== 'history_type' && editField !== 'tags'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Point Write Dialog -->
    <point-write-dialog
      :is-open="dialogPointWrite"
      :on-close="() => (dialogPointWrite = false)"
      :selected-point="editedItem"
      :api="'rubix'"
      :response-from-dialog="response => responseFromDialog(response)"
    ></point-write-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import RdPointTags from './RdPointTags';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import PointWriteDialog from '../Common/PointWriteDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { getTypeNames, getTypeTags } from '../../utils/tagsUtils';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, TOPIC, POINT_DEFAULT, POINT_ADVANCED_HEADERS, POINT_EDIT_ALL_DEFAULT } from './rubixDriverConstant';
import {
  isJsonString,
  copyToClipboard,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  convertPointsValueToString,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    RdPointTags,
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    PointWriteDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    conventionNaming: conventionNaming,
    tableProps: { ...DATA_TABLE_PROPS },
    topic: TOPIC,
    rules: { ...RULES },
    pointHeaders: [...POINT_ADVANCED_HEADERS],
    editedItem: { ...POINT_DEFAULT },
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    dialog: false,
    dialogDelete: false,
    dialogPointWrite: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    tab: 0,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    selected: [],
    tags: [],
    dataImport: '',
    importArray: [],
    dirs: [],
    e1: 1,
    search: '',
    searchItems: [],
    searchWizard: '',
    editField: '',
    intervalId: null,
    isClickSaveImport: false,
    typeNames: getTypeNames(),
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.rdNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    'tags'(val) {
      this.onChangeTag(val);
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getData();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');
      next();
    });
  },
  mounted() {
    this.getData();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getData();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    onChangeTag(tags) {
      if (tags.length == 0) return;
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({ ...objTags });
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
    async getData() {
      this.loadingData = true;
      this.device = await axios.get(`point-server/generic/device/${this.$route.params.id}?with_children=true`);
      this.getDir();
      this.editedItem.device_uuid = this.device.uuid;
      this.device.points = this.device.points.map(item => {
        const keys = Object.keys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
      this.loadingData = false;
    },
    getDir() {
      axios.get(`point-server/generic/network/${this.device.network_uuid}`).then(data => {
        (this.dirs = [{ text: 'NETWORKS', link: '/rubix-driver/networks', disabled: false }]),
          this.dirs.push({
            text: `NET: ${data.name}`,
            link: `/rubix-driver/networks/${data.uuid}/devices`,
            disabled: false,
          });
        this.dirs.push({ text: `DEV: ${this.device.name}`, disabled: true });
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
        const tags = JSON.parse(item.tags);
        for (const tag in tags) {
          arrTags.push({ key: tag, value: tags[tag] });
        }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        this.$refs.comboBox.blur();
        this.$nextTick(() => {
          if (this.editedIndex > -1) {
            this.updatePoint(this.editedItem);
          } else {
            this.createPoint(this.editedItem);
          }
        });
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.editedItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/point/${this.editedItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.uuid });
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT, device_uuid: this.device.uuid });
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      (this.isClickSaveImport = false),
        this.$nextTick(() => {
          this.dataImport = '';
        });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try {
            const promises = [];
            for (const item of this.importArray) {
              item.device_uuid = this.device.uuid; //update to the current device_uuid
              if (item.priority_array_write && typeof item.priority_array_write == 'string') {
                item.priority_array_write = JSON.parse(item.priority_array_write);
              }
              if (!item.uuid) {
                promises.push(new Promise((resolve, reject) => this.createPoint(item, resolve, reject)));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {}
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point => {
        delete point.uuid;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      return _find(points, { [fieldName]: value }) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('points', data);
      }
      if (type === 'excel') {
        return exportExcel('points', data);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(data));
      }
    },
    updatePoint(data) {
      data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
      axios
        .put(`point-server/generic/point/${data.uuid}`, data)
        .then(response => {
          const index = this.device.points.findIndex(n => n.uuid == response.uuid);
          Object.assign(this.device.points[index], response);
          this.close();
          this.$toasted.show('update success');
        })
        .catch(error => {
          this.$toasted.show('update fail', { type: 'error' });
        });
    },
    async createPoint(data, resolve, reject) {
      try {
        data.fallback_value = !isNaN(parseFloat(data.fallback_value)) ? parseFloat(data.fallback_value) : null;
        const response = await axios.post(`point-server/generic/point`, data);
        this.device.points.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if (index > -1) {
            Object.assign(this.importArray[index], response);
          }
        }
        this.close();
        this.$toasted.show('create success');
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
      this.tags = [];
    },
    numberChange() {
      if (!this.editAll[this.editField] && this.editField !== 'fallback_value')
        return (this.editAll[this.editField] = 0);
    },
    cancelEdit() {},
    openEdit() {},
    closeEdit() {},
    onClickRefresh() {
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onChangeTypeName(val) {
      if (!this.typeNames.includes(val)) return;
      const arrTags = (this.tags = []);
      const typeTags = getTypeTags(val);
      for (const tag of typeTags) {
        arrTags.push({ key: tag, value: tag });
      }
      this.tags = arrTags;
    },
    openPointWriteDialog(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogPointWrite = true;
    },
    responseFromDialog(point_updated) {
      const index = this.device.points.findIndex(p => p.uuid == point_updated.uuid);
      Object.assign(this.device.points[index], point_updated);
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>

